<template>
    <div id="widget-clock" class="absolute top-0 left-0">
        <div class="cod-date mb-2 mx-2 text-l text-white text-center">
            {{dc.yy}}年{{dc.mo}}月{{dc.dd}}日{{dc.wdCHN}} 
        </div>
        <div class="cod-time m-auto space-x-4 text-gray-100 tracking-wider">
            <span class="cod-hour text-9xl" v-text="dc.hh"></span>
            <span class="cod-separator inline-block text-7xl">:</span>
            <span class="cod-minute text-9xl" v-text="dc.mm"></span>
            <span class="cod-apm inline-block text-2xl align-top font-semibold tracking-tighter my-3" v-text="dc.apm"></span>
        </div>
    </div>
</template>

<script>


export default {
  name: 'clockDigits',
  methods: {
      updateTime() {
        let now = new Date()
        let hh = now.getHours()
        let apm = hh > 11 ? 'pm' : 'am'
        if (hh<10) {
            hh = "0" + hh
        }
        if (hh>12) {
            hh = "0" + (hh - 12)
        }
        let mm = now.getMinutes()
        if (mm<10) {
            mm = "0" + mm
        }
        let ss = now.getSeconds()
        if (ss<10) {
            ss = "0" + ss
        }
        let yy = now.getFullYear()
        let mo = now.getMonth() + 1
        let dd = now.getDate()
        let wdStrings = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"]
        let wd = wdStrings[now.getDay()]
        let wdStringsCHN = ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"]
        let wdCHN = wdStringsCHN[now.getDay()]
        return {
            apm: apm,
            hh: hh,
            mm: mm,
            ss: ss,
            yy: yy,
            mo: mo,
            dd: dd,
            wd: wd,
            wdCHN: wdCHN
        }
      }
  },
  mounted() {
      var self = this
      function refreshSet () {
        self.dc = self.updateTime()
      }
      setInterval(refreshSet, 1000)
  },
  data: function() {
      return {
          dc: this.updateTime()
      }
  }
}
</script>

