<template>
    <div id="widget-weather" class="absolute top-0 left-0">
       <div class="cod-weather mb-3 mx-2 text-l text-white text-center">
          {{weatherData}}
        </div>
    </div>
</template>

<script>
import axios from "axios"

export default {
  name: 'weatherBaidu',
  data: function() {
    return {
      weatherData: 'Loading weather...'
    }
  },
  methods: {
    refreshWeather() {
      axios.get("https://api.66mz8.com/api/weather.php?location=上海")
      .then(response => {
        if (response.data) {
          console.log(response.data)
          let wdata = response.data.data[0]
          let temp = wdata.temperature.split('/')
          let loTemp = temp[1].replace(/℃/g, '')
          let hiTemp = temp[0].replace(/℃/g, '')
          this.weatherData = wdata.weather + " · " + loTemp + " ~ " + hiTemp + "℃"
        }
      })
    }
  },
  mounted() {
     this.refreshWeather()
     setInterval(this.refreshWeather, 600000)
  }
}
</script>

